function Home(props) {
	const navigationPrevRef = useRef();
	const navigationNextRef = useRef();
	const [products, setProducts] = useState([]);

	const dispatch = useDispatch();

	useEffect(() => {
		const getAllProductApi = async () => {
			try {
				const response = await productApi.getAll();
				setProducts(response.products);
				dispatch(addProduct(response.products));
			} catch (error) {
				console.log('Faild to fetch user list: ', error);
			}
		};
		getAllProductApi();
	}, []);

	console.log('products', products);

	return (
		<div className="home">
			<Swiper
				modules={[Navigation, Pagination, Scrollbar, A11y]}
				{...swiperOptions}
				pagination={{ clickable: true }}
				slidesPerView={1} // s lng slide 1 lc trn mn hnh
				autoplay={{ delay: 2000 }}
				navigation={{
					prevEl: navigationPrevRef.current,
					nextEl: navigationNextRef.current,
				}}
			>
				<SwiperSlide>
					<img src={slide1} alt="" width="100%" height="100%" />
				</SwiperSlide>
				<SwiperSlide>
					<img src={slide2} alt="" width="100%" height="100%" />
				</SwiperSlide>
				<SwiperSlide>
					<img src={slide3} alt="" width="100%" height="100%" />
				</SwiperSlide>
				<div ref={navigationPrevRef} />
				<div ref={navigationNextRef} />
			</Swiper>

			<div className="header-category">
				<h3 className="category-title">Danh mc sn phm</h3>
				<div className="category-list">
					{listCategory.map((itemCategory, index) => (
						<div className="category-item" key={index}>
							<Link className="category-link" to="/category">
								<img
									src={itemCategory.image}
									alt=""
									width="60"
								/>
								<p className="category-name">
									{itemCategory.name}
								</p>
							</Link>
						</div>
					))}
				</div>
			</div>

			<div className="header-product">Gi  hm nay</div>

			<div className="home-product">
				{products.map((productItem, index) => (
					<ProductItem key={index} index={index} {...productItem} />
				))}
			</div>
		</div>
	);
}

